/**
 * @description 
 * @param {T[]} array 
 * @param {number} subGroupLength 
 * @returns {T[][]} 
 */
export const groupSplitArray = <T>(
  array: T[],
  subGroupLength: number
): T[][] => {
  let index = 0;
  const newArray = [];
  while (index < array.length) {
    newArray.push(array.slice(index, (index += subGroupLength)));
  }
  return newArray;
};
