import axios from 'axios';
import { IDictionary } from '../';

/**
 * @description axiso 
 * @param {number[]} lpXis X
 * @param {number[]} lpYis Y
 * @param {number[]} lpCis 
 * @param {string} lib 
 */
export const getWordFromHandWrite: (
  lpXis: number[],
  lpYis: number[],
  lpCis: number[],
  lib: string
) => IDictionary<any> = async (
  lpXis: number[],
  lpYis: number[],
  lpCis: number[],
  lib: string
) => await axios.post('', { lib, lpXis, lpYis, lpCis });
